* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.card {
  width: 350px;
  border-radius: 5px;
  padding: 20px;
  transition: box-shadow .3s;
  box-shadow: 5px 5px 5px 5px gray;
}

.card:hover {
  box-shadow: 5px 5px 5px 5px green;
}

img {
  width: 300px;
}

.modal {
  width: 700px;
  background-color: #fff;
  padding: 30px;
  position: relative;
}

.delete {
  z-index: 999;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  position: absolute;
  top: 10px;
  right: 10px;
}

.js-list .delete:after, .js-basket .delete:after {
  content: "";
  color: green;
  font-family: FontAwesome;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.652;
}

.modal-img {
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.modal-price {
  margin-bottom: 10px;
}

.main, .favorite, .checkout {
  max-width: 1200px;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

.btn {
  color: #fff;
  min-height: 30px;
  min-width: 120px;
  cursor: pointer;
  background-color: green;
  border: none;
  border-radius: 10px;
  padding: 15px;
}

.btn:hover {
  background-color: #002ead;
  transition: all .7s;
}

.button-box {
  justify-content: space-around;
  display: flex;
}

.title-name, .modal-title {
  text-align: center;
  margin-bottom: 10px;
  font-size: 26px;
  font-weight: 700;
  line-height: 1.625;
}

.information-link {
  cursor: pointer;
  text-align: center;
  color: green;
  margin-bottom: 20px;
  font-size: 20px;
  line-height: 1.52;
  display: block;
}

.information-link:hover {
  color: #002ead;
}

#button {
  width: 50px;
  height: 50px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  background-color: green;
  border-radius: 5px;
  transition: background-color .3s, opacity .5s, visibility .5s;
  display: inline-block;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

#button:after {
  content: "";
  color: #fff;
  font-family: FontAwesome;
  font-size: 2em;
  font-style: normal;
  font-weight: normal;
  line-height: 50px;
}

#button:hover {
  cursor: pointer;
  background-color: #002ead;
}

#button.show {
  opacity: 1;
  visibility: visible;
}

.flip {
  position: relative;
}

.flip > .front, .flip > .back {
  transition-property: transform, opacity;
  transition-duration: 1.5s;
  transition-timing-function: cubic-bezier(.175, .885, .32, 1.275);
  display: block;
}

.flip > .front {
  transform: rotateY(0deg);
}

.flip > .back {
  opacity: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateY(-180deg);
}

.flip:hover > .front {
  transform: rotateY(180deg);
}

.flip:hover > .back {
  opacity: 1;
  transform: rotateY(0deg);
}

.flip.flip-vertical > .back {
  transform: rotateX(-180deg);
}

.flip.flip-vertical:hover > .front {
  transform: rotateX(180deg);
}

.flip.flip-vertical:hover > .back {
  transform: rotateX(0deg);
}

.navigation {
  color: #fff;
  text-align: center;
  background-color: green;
  border-radius: 5px;
  flex-grow: 1;
  padding: 10px 15px;
  text-decoration: none;
  transition-duration: .7s;
  transition-timing-function: cubic-bezier(.175, .885, .32, 1.275);
  display: inline-block;
}

.visited, .navigation:hover, .navigation:active {
  background-color: #002ead;
}

nav {
  justify-content: space-around;
  gap: 30px;
  margin-top: 20px;
  display: flex;
}

#NotiflixNotifyWrap {
  margin-top: 10px;
  margin-right: 10px;
}

.basicLightbox {
  width: 100%;
  height: 100vh;
  opacity: .01;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
}

.basicLightbox__placeholder {
  max-width: 100%;
  z-index: 1;
  will-change: transform;
  transition: transform .4s;
  transform: scale(.9);
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  max-width: 95%;
  max-height: 95%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  pointer-events: auto;
}

.basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  width: auto;
  height: auto;
}

.basicLightbox--iframe .basicLightbox__placeholder, .basicLightbox--img .basicLightbox__placeholder, .basicLightbox--video .basicLightbox__placeholder {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

